<template>
  <router-view></router-view>
</template>

<style>
@import '@/assets/styles/common.scss';
</style>

<script setup>
import { onBeforeMount } from 'vue'
import { useUserStore } from '@/store/modules/user'
import { getCookie } from '@/utils/cookie'
import { useRouter, useRoute } from 'vue-router'

const userStore = useUserStore()
const router = useRouter()
const route = useRoute()

onBeforeMount(async () => {
  try {
    console.log('App mounted - 当前路径:', route.path)
    const token = getCookie('token')
    console.log('App mounted - token:', token)
    if (!token) {
      console.log('App mounted - 无token，准备重定向')
      const currentPath = window.location.pathname
      if (currentPath.startsWith('/teacher')) {
        console.log('App mounted - 重定向到教师登录')
        router.replace('/teacher/login')
      } else if (currentPath.startsWith('/system-management')) {
        console.log('App mounted - 重定向到管理员登录')
        router.replace('/system-management/auth')
      } else {
        console.log('App mounted - 重定向到学生登录')
        router.replace('/student/login')
      }
      return
    }
    await userStore.restoreFromStorage()
    if (!userStore.userInfo) {
      const currentPath = route.path
      if (currentPath.includes('/system-management')) {
        router.push('/system-management/auth')
      } else if (currentPath.includes('/teacher')) {
        router.push('/teacher/login')
      } else {
        router.push('/student/login')
      }
    }
  } catch (error) {
    console.error('App mount error:', error)
    userStore.logout()
    const currentPath = route.path
    if (currentPath.includes('/system-management')) {
      router.push('/system-management/auth')
    } else if (currentPath.includes('/teacher')) {
      router.push('/teacher/login')
    } else {
      router.push('/student/login')
    }
  }
})
</script>
