import { createRouter, createWebHistory } from 'vue-router'
import { useUserStore } from '@/store/modules/user'
import { getCookie } from '@/utils/cookie'

const routes = [
  {
    path: '/',
    redirect: () => {
      // 根据当前路径判断重定向目标
      const currentPath = window.location.pathname
      if (currentPath.startsWith('/teacher')) {
        return '/teacher/login'
      } else if (currentPath.startsWith('/system-management')) {
        return '/system-management/auth'
      }
      return '/student/login'
    }
  },
  // 学生端路由
  {
    path: '/student',
    name: 'StudentLayout',
    component: () => import('@/views/student/student-home.vue'),
    meta: { requiresAuth: true, userType: 'student' },
    redirect: '/student/dashboard',
    children: [
      {
        path: 'dashboard',
        name: 'StudentDashboard',
        component: () => import('@/views/student/student-dashboard.vue'),
        meta: { title: '首页' }
      },
      {
        path: 'group',
        name: 'StudentGroup',
        component: () => import('@/views/student/student-group.vue'),
        meta: { title: '小组大厅' }
      },
      {
        path: 'settings',
        name: 'StudentSettings',
        component: () => import('@/views/student/student-settings.vue'),
        meta: { title: '设置' }
      },
      {
        path: 'my-group',
        name: 'StudentMyGroup',
        component: () => import('@/views/student/my-group.vue'),
        meta: { title: '我的小组' }
      }
    ]
  },
  {
    path: '/student/login',
    name: 'StudentLogin',
    component: () => import('@/views/student/student-login.vue')
  },
  // 教师端路由
  {
    path: '/teacher',
    name: 'TeacherLayout',
    component: () => import('@/views/teacher/teacher-home.vue'),
    meta: { requiresAuth: true, userType: 'teacher' },
    redirect: '/teacher/dashboard',
    children: [
      {
        path: 'dashboard',
        name: 'TeacherDashboard',
        component: () => import('@/views/teacher/teacher-dashboard.vue'),
        meta: { title: '首页' }
      },
      {
        path: 'my-groups',
        name: 'TeacherMyGroups',
        component: () => import('@/views/teacher/my-groups.vue'),
        meta: { title: '我的小组' }
      },
      {
        path: 'group-hall',
        name: 'TeacherGroupHall',
        component: () => import('@/views/teacher/group-hall.vue'),
        meta: { title: '小组大厅' }
      },
      {
        path: 'settings',
        name: 'TeacherSettings',
        component: () => import('@/views/teacher/teacher-settings.vue'),
        meta: { title: '设置' }
      }
    ]
  },
  {
    path: '/teacher/login',
    name: 'TeacherLogin',
    component: () => import('@/views/teacher/teacher-login.vue')
  },
  // 管理员端路由
  {
    path: '/system-management',
    name: 'AdminLayout',
    component: () => import('@/views/admin/admin-home.vue'),
    meta: { requiresAuth: true, userType: 'admin' },
    redirect: '/system-management/dashboard',
    children: [
      {
        path: 'dashboard',
        name: 'AdminDashboard',
        component: () => import('@/views/admin/admin-dashboard.vue'),
        meta: { title: '首页' }
      },
      {
        path: 'manage',
        name: 'AdminManage',
        component: () => import('@/views/admin/admin-manage.vue'),
        meta: { title: '管理' },
        redirect: '/system-management/manage/student',
        children: [
          {
            path: 'student',
            name: 'StudentManage',
            component: () => import('@/views/admin/manage/student-manage.vue'),
            meta: { title: '学生管理' }
          },
          {
            path: 'teacher',
            name: 'TeacherManage',
            component: () => import('@/views/admin/manage/teacher-manage.vue'),
            meta: { title: '教师管理' }
          },
          {
            path: 'group',
            name: 'GroupManage',
            component: () => import('@/views/admin/manage/group-manage.vue'),
            meta: { title: '小组管理' }
          }
        ]
      },
      {
        path: 'config',
        name: 'ConfigManage',
        component: () => import('@/views/admin/config-manage.vue'),
        meta: { title: '配置管理' }
      },
      {
        path: 'settings',
        name: 'AdminSettings',
        component: () => import('@/views/admin/admin-settings.vue'),
        meta: { title: '设置' }
      }
    ]
  },
  {
    path: '/system-management/auth',
    name: 'AdminLogin',
    component: () => import('@/views/admin/admin-login.vue')
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 路由守卫
router.beforeEach(async (to, from, next) => {
  console.log('路由守卫 - 目标路径:', to.path)
  console.log('路由守卫 - 来源路径:', from.path)
  const token = getCookie('token')
  const userType = getCookie('userType')
  console.log('路由守卫 - token:', token)
  console.log('路由守卫 - userType:', userType)
  const userStore = useUserStore()

  // 添加一个标志来标记路由切换状态
  if (window._routeChanging) {
    console.log('路由守卫 - 路由切换中，阻止重复切换')
    next(false)
    return
  }
  window._routeChanging = true

  // 安全检查：如果有 userInfo 但没有 configInfo，说明处于中间状态
  if (userStore.userType === 'student' && 
      (!userStore.userInfo || (userStore.userInfo && !userStore.configInfo)) && 
      !to.path.includes('/login')) {
    userStore.logout()  // 清理状态
    next('/student/login')  // 强制跳转到登录页
    window._routeChanging = false  // 确保重置标志
    return
  }

  try {
    if (to.meta.requiresAuth) {
      if (!token) {
        // 未登录，跳转到对应的登录页
        switch (to.meta.userType) {
          case 'student':
            next('/student/login')
            break
          case 'teacher':
            next('/teacher/login')
            break
          case 'admin':
            next('/system-management/auth')
            break
          default:
            next('/student/login')
        }
      } else if (to.meta.userType !== userType) {
        // 用户类型不匹配，跳转到对应的主页
        switch (userType) {
          case 'student':
            next('/student')
            break
          case 'teacher':
            next('/teacher')
            break
          case 'admin':
            next('/system-management')
            break
          default:
            next('/student/login')
        }
      } else {
        next()
      }
    } else {
      // 如果已登录且访问登录页，重定向到主页
      if (token && to.path.includes('login')) {
        switch (userType) {
          case 'student':
            next('/student')
            break
          case 'teacher':
            next('/teacher')
            break
          case 'admin':
            next('/system-management')
            break
          default:
            next()
        }
      } else {
        next()
      }
    }
  } catch (error) {
    console.error('Route guard error:', error)
    userStore.logout()
    // 根据当前路径判断重定向目标
    const currentPath = to.path
    if (currentPath.includes('/system-management')) {
      next('/system-management/auth')
    } else if (currentPath.includes('/teacher')) {
      next('/teacher/login')
    } else {
      next('/student/login')
    }
  } finally {
    window._routeChanging = false
  }
})

// 添加全局后置钩子
router.afterEach(() => {
  // 确保在路由切换完成后重置标志
  window._routeChanging = false
})

export default router 