import request from '@/utils/request'

// 获取所有配置
export function getAllConfigs() {
  return request({
    url: '/api/Config',
    method: 'get'
  })
}

// 添加配置
export function addConfig(data) {
  return request({
    url: '/api/Config',
    method: 'post',
    data
  })
}

/**
 * 获取年级专业配置信息
 * @param {string} gde - 年级
 * @param {string} mjr - 专业
 * @returns {Promise<Object>} 配置信息
 */
export function getConfig(gde, mjr) {
  return request({
    url: '/api/Config/getByGdeAndMjr',
    method: 'get',
    params: { gde, mjr }
  })
}

// 通过年级专业获取配置
export function getConfigByGradeAndMajor(gde, mjr) {
  return request({
    url: '/api/Config/getByGdeAndMjr',
    method: 'get',
    params: { gde, mjr }
  })
}

// 删除配置
export function deleteConfig(id) {
  return request({
    url: `/api/Config/${id}`,
    method: 'post'
  })
}

// 更新配置
export function updateConfig(configId, data) {
  return request({
    url: `/api/Config/update/${configId}`,
    method: 'post',
    data
  })
}