import axios from 'axios'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useUserStore } from '@/store/modules/user'
import router from '@/router'
import { getCookie } from '@/utils/cookie'

const service = axios.create({
  baseURL: 'http://172.17.140.70:8000',
  timeout: 10000
})

// 请求拦截器
service.interceptors.request.use(
  async config => {
    const token = getCookie('token')
    const userType = getCookie('userType')
    const userStore = useUserStore()

    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`
      
      // 如果是学生账号，检查登录状态
      if (userType === 'student' && 
          userStore.userInfo && 
          !config.url.includes('/login') && 
          !config.url.includes('/login-record')) {
        try {
          const loginRecord = await axios.get(
            `${service.defaults.baseURL}/api/Student/login-record`,
            { 
              params: { account: userStore.userInfo.stu_num }
            }
          )
          
          const lastLoginTime = getCookie('loginTime')
          
          // 移除时区信息再比对
          const serverTime = loginRecord.data.split('+')[0]
          const localTime = lastLoginTime
          if (serverTime !== localTime) {
            // 先显示提示框
            await ElMessageBox.alert(
              '您的账号已在其他设备登录，如非本人操作，请及时修改密码。',
              '登录提示',
              {
                confirmButtonText: '确定',
                type: 'warning',
                showClose: false,
                callback: () => {
                  userStore.logout()
                  router.replace('/student/login')
                }
              }
            )
            return Promise.reject(new Error('账号已在其他地方登录'))
          }
        } catch (error) {
          console.error('检查登录状态失败:', error)
        }
      }
    }
    
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// 响应拦截器
service.interceptors.response.use(
  response => {
    // 处理 204 No Content 的情况
    if (response.status === 204) {
      return true
    }

    const res = response.data
    // 如果响应成功且有数据，直接返回数据
    if (res && response.status === 200) {
      return res
    }
    // 如果是预选记录接口且返回 404，返回空数据
    if (response.config.url.includes('/PreselectStudent/student/') && response.status === 404) {
      return { teachers: [] }
    }
    // 如果有错误信息则显示
    ElMessage.error(res?.message || '请求错误')
    return Promise.reject(res)
  },
  error => {
    // 如果是预选记录接口且返回 404，返回空数据
    if (error.response?.status === 404 && 
        error.config.url.includes('/PreselectStudent/student/')) {
      return { teachers: [] }
    }
    ElMessage.error(error.message || '网络错误')
    return Promise.reject(error)
  }
)

export default service 