import request from '@/utils/request'

// 学生登录
export function studentLogin(data) {
  return request({
    url: '/api/Student/login',
    method: 'post',
    params: {
      stu_num: data.username,
      stu_pwd: data.password
    }
  })
}

// 教师登录
export function teacherLogin(data) {
  return request({
    url: '/api/Teacher/login-name',
    method: 'post',
    params: {
      tea_name: data.username,
      tea_pwd: data.password
    }
  })
}

// 管理员登录
export function adminLogin(data) {
  return request({
    url: '/api/Admin/login',
    method: 'post',
    data: {
      adm_name: data.username,
      adm_pwd: data.password
    }
  })
} 