import request from '@/utils/request'

// 更新教师密码
export function updateTeacherPassword(id, newPassword) {
  return request({
    url: '/api/Teacher/update-password',
    method: 'post',
    params: {
      id,
      newPassword
    }
  })
}

// 更新教师信息
export function updateTeacherInfo(id, data) {
  return request({
    url: `/api/Teacher/update-teacher/${id}`,
    method: 'post',
    data
  })
}

// 获取教师的小组（按年级）
export function getTeacherGroups(id, gde) {
  return request({
    url: `/api/Teacher/get-groupbygde/${id}`,
    method: 'get',
    params: { gde }
  })
}

// 预选小组
export function preselectGroups(teacherId, groupIds) {
  return request({
    url: `/api/PreselectTeacher/teacher/${teacherId}`,
    method: 'post',
    data: groupIds
  })
}

// 获取教师预选记录
export function getTeacherPreselectRecord(teacherId) {
  return request({
    url: `/api/PreselectTeacher/teacher/${teacherId}`,
    method: 'get'
  })
}

// 获取所有教师
export function getAllTeachers() {
  return request({
    url: '/api/Teacher',
    method: 'get'
  })
}

// 添加教师
export function addTeacher(data) {
  return request({
    url: '/api/Teacher',
    method: 'post',
    data
  })
}

// 更新教师信息
export function updateTeacherInfoByAdmin(id, data) {
  return request({
    url: `/api/Teacher/update-teacher/${id}`,
    method: 'post',
    data
  })
}

// 重置教师密码
export function resetTeacherPassword(id, newPassword) {
  return request({
    url: '/api/Teacher/update-password',
    method: 'post',
    params: {
      id,
      newPassword
    }
  })
}

// 删除教师
export function deleteTeacher(id) {
  return request({
    url: `/api/Teacher/${id}`,
    method: 'post'
  })
}

// 获取专业教师列表
export function getTeachersByMajor(majorName) {
  return request({
    url: '/api/Teacher/search-teacher',
    method: 'get',
    params: {
      mjr_name: majorName
    }
  })
} 