import request from '@/utils/request'

// 获取所有小组
export function searchGroups(gde, mjr) {
  return request({
    url: '/api/Group/search-group',
    method: 'get',
    params: { gde, mjr }
  })
}

// 获取特定小组信息
export function getGroupById(id) {
  return request({
    url: `/api/Group/get/${id}`,
    method: 'get'
  })
}

// 获取小组学生
export function getGroupStudents(id) {
  return request({
    url: `/api/Group/get-allstu/${id}`,
    method: 'get'
  })
}

// 获取小组导师
export function getGroupTeacher(id) {
  return request({
    url: `/api/Group/get-alltea/${id}`,
    method: 'get'
  })
}

// 创建小组
export function createGroup(stu_id, groupData) {
  return request({
    url: `/api/Group/${stu_id}`,
    method: 'post',
    data: groupData
  })
}

// 修改小组信息
export function updateGroupInfo(groupData) {
  return request({
    url: '/api/Group/update-info',
    method: 'post',
    data: groupData
  })
}

// 添加组员
export function addGroupMembers(groupId, studentIds) {
  return request({
    url: `/api/Group/add-stu/${groupId}`,
    method: 'post',
    data: studentIds
  })
}

// 移除组员
export function removeGroupMember(groupId, studentId) {
  return request({
    url: '/api/Group/remove-stu',
    method: 'post',
    params: {
      g_id: groupId,
      s_id: studentId
    }
  })
}

// 解散小组
export function dismissGroup(groupId, studentId) {
  return request({
    url: '/api/Group/dismiss',
    method: 'post',
    params: {
      id: groupId,
      stu_id: studentId
    }
  })
}

// 预选导师
export function preselectTeachers(groupId, teacherIds) {
  return request({
    url: `/api/PreselectStudent/student/${groupId}`,
    method: 'post',
    data: teacherIds
  })
}

// 获取专业教师列表
export function getTeachersByMajor(majorName) {
  return request({
    url: '/api/Teacher/search-teacher',
    method: 'get',
    params: {
      mjr_name: majorName
    }
  })
}

// 获取预选记录
export function getPreselectRecord(groupId) {
  return request({
    url: `/api/PreselectStudent/student/${groupId}`,
    method: 'get'
  })
}

/**
 * 获取学生信息（不包括登录时间和配置）
 * @param {number} studentId - 学生ID
 * @returns {Promise<Object>} 学生信息
 */
export function getUserInfo(studentId) {
  return request({
    url: `/api/Student/${studentId}`,
    method: 'get'
  })
}

// 获取未预选的小组
export function getUnselectedGroups(grade, major) {
  return request({
    url: '/api/Group/unselected',
    method: 'get',
    params: { grade, major }
  })
}

// 获取未满人的小组
export function getNotFullGroups(grade, major) {
  return request({
    url: '/api/Group/not-full',
    method: 'get',
    params: { grade, major }
  })
}

// 获取未分配教师的小组
export function getGroupsWithoutTeacher(grade, major) {
  return request({
    url: '/api/Group/without-teacher',
    method: 'get',
    params: { grade, major }
  })
}

// 创建小组
export function createGroupByAdmin(data) {
  return request({
    url: '/api/Group',
    method: 'post',
    data
  })
}

// 创建小组并分配组长
export function createGroupWithLeader(studentId, data) {
  return request({
    url: `/api/Group/${studentId}`,
    method: 'post',
    data
  })
}

// 删除小组
export function deleteGroup(id) {
  return request({
    url: `/api/Group/delet/${id}`,
    method: 'post'
  })
}

// 为小组添加教师
export function addTeacherToGroup(groupId, teacherId) {
  return request({
    url: `/api/Group/add-tea/${groupId}`,
    method: 'post',
    params: { tea_id: teacherId }
  })
}

// 从小组移除教师
export function removeTeacherFromGroup(groupId, teacherId) {
  return request({
    url: '/api/Group/remove-tea',
    method: 'post',
    params: {
      g_id: groupId,
      t_id: teacherId
    }
  })
}

// 通过学生姓名或学号搜索小组
export function searchGroupsByStudent(studentName) {
  return request({
    url: '/api/Group/search-by-student',
    method: 'get',
    params: { studentName }
  })
}

// 获取预选了指定教师的小组列表
export function getGroupsPreselectTeacher(teacherId) {
  return request({
    url: `/api/Group/preselected-by-teacher/${teacherId}`,
    method: 'get'
  })
}

// 获取已分配导师的小组
export function getGroupsWithTeacher(grade, major) {
  return request({
    url: '/api/Group/with-teacher',
    method: 'get',
    params: { grade, major }
  })
}

// 获取分配结果
export function getAllocations(gradeId, majorId) {
  return request({
    url: '/api/PreselectTeacher',
    method: 'get',
    params: {
      gde_id: gradeId,
      mjr_id: majorId
    }
  })
}