import { defineStore } from 'pinia'
import { studentLogin, teacherLogin, adminLogin } from '@/api/user'
import { getTeacherPreselectRecord } from '@/api/teacher'
import { getGroupById } from '@/api/group'
import { getConfig } from '@/api/config'
import { ElMessage } from 'element-plus'
import router from '@/router'
import { setCookie, getCookie, removeCookie } from '@/utils/cookie'

export const useUserStore = defineStore('user', {
  state: () => ({
    userInfo: null,
    configInfo: null,
    token: '',
    userType: '',
    isLoading: false,
    teacherPreselects: []
  }),

  actions: {
    async login(loginForm, type) {
      try {
        let res
        switch (type) {
          case 'student':
            res = await studentLogin(loginForm)
            if (res.status === 1) {
              this.userInfo = res.studentDto
              this.token = res.loginTime
              this.userType = 'student'
              const loginTime = res.loginTime.split('+')[0]
              setCookie('loginTime', loginTime)
              await this.getConfigAction()
            } else {
              throw new Error(res.message || '登录失败')
            }
            break
          case 'teacher':
            res = await teacherLogin(loginForm)
            if (res.status === 1) {
              this.userInfo = res.teacherDto
              this.token = res.loginTime
              this.userType = 'teacher'
              try {
                const preselectRecord = await getTeacherPreselectRecord(res.teacherDto.tea_id)
                if (preselectRecord?.group?.length) {
                  const groupDetails = await Promise.all(
                    preselectRecord.group.map(groupId => getGroupById(groupId))
                  )
                  localStorage.setItem('teacherSelectedGroups', JSON.stringify(groupDetails))
                } else {
                  localStorage.removeItem('teacherSelectedGroups')
                }
              } catch (error) {
                console.error('获取教师预选记录失败:', error)
                localStorage.removeItem('teacherSelectedGroups')
              }
            } else {
              throw new Error(res.message || '登录失败')
            }
            break
          case 'admin':
            res = await adminLogin(loginForm)
            if (res.status === 0) {
              this.userInfo = {
                adm_id: res.adm_id,
                adm_name: res.adm_name
              }
              this.token = res.token
              this.userType = 'admin'
            } else {
              throw new Error(res.message || '登录失败')
            }
            break
          default:
            throw new Error('未知的用户类型')
        }
        
        // 保存到 cookie
        setCookie('token', this.token)
        setCookie('userInfo', JSON.stringify(this.userInfo))
        setCookie('userType', this.userType)
        
        return res
      } catch (error) {
        // 清理脏数据
        this.token = ''
        this.userInfo = null
        this.userType = ''
        this.configInfo = null
        removeCookie('token')
        removeCookie('userInfo')
        removeCookie('userType')
        removeCookie('configInfo')
        removeCookie('loginTime')
        
        // 根据用类型显示不同的错误提示
        let errorMsg = error.message || '登录失败'
        switch (type) {
          case 'student':
            errorMsg += '，初始密码为学号后六位'
            break
          case 'teacher':
            errorMsg += '，初始密码为gdmu'
            break
        }
        ElMessage.error(errorMsg)
        throw error
      }
    },

    // 获取配置信息
    async getConfigAction() {
      try {
        if (this.userType === 'student' && this.userInfo) {
          const config = await getConfig(
            this.userInfo.stu_gde,
            this.userInfo.stu_mjr
          )
          setCookie('configInfo', JSON.stringify(config))
          this.configInfo = config
        }
      } catch (error) {
        this.configInfo = null
        removeCookie('configInfo')
        ElMessage.error('获取配置信息失败')
        if (this.userType === 'student' && !this.configInfo) {
          this.logout()
          router.replace('/student/login')
        }
      }
    },

    // 从 cookie 恢复状态
    async restoreFromStorage() {
      try {
        console.log('Store - 开始恢复状态')
        const storedUserInfo = getCookie('userInfo')
        const storedConfig = getCookie('configInfo')
        const storedUserType = getCookie('userType')
        console.log('Store - 存储的用户类型:', storedUserType)
        console.log('Store - 存储的用户信息:', storedUserInfo)
        
        if (storedUserInfo) {
          try {
            this.userInfo = JSON.parse(storedUserInfo)
            this.userType = storedUserType
            console.log('Store - 状态恢复成功')
            
            if (storedConfig) {
              try {
                this.configInfo = JSON.parse(storedConfig)
              } catch (error) {
                console.error('Store - 配置信息解析失败:', error)
                this.configInfo = null
                removeCookie('configInfo')
                if (this.userType === 'student') {
                  await this.getConfigAction()
                }
              }
            } else if (this.userType === 'student') {
              await this.getConfigAction()
            }
          } catch (error) {
            console.error('Store - 用户信息解析失败:', error)
            this.logout()
            ElMessage.error('登录状态已失效，请重新登录')
            router.replace(`/${storedUserType}/login`)
          }
        }
      } catch (error) {
        this.logout()
        ElMessage.error('登录状态已失效，请重新登录')
        const userType = getCookie('userType') || 'student'
        router.replace(`/${userType}/login`)
      }
    },

    // 登出
    logout() {
      this.userInfo = null
      this.configInfo = null
      this.token = ''
      this.userType = ''
      if (this.userType === 'teacher') {
        localStorage.removeItem('teacherSelectedGroups')
      }
      removeCookie('userInfo')
      removeCookie('configInfo')
      removeCookie('token')
      removeCookie('userType')
      removeCookie('loginTime')
    },

    // 设置配置信息
    setConfigInfo(config) {
      this.configInfo = config
      setCookie('configInfo', JSON.stringify(config))
    }
  }
}) 