import { createApp } from 'vue'
import { createPinia } from 'pinia'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import App from './App.vue'
import router from './router'

// 处理 ResizeObserver 错误
window.ResizeObserver = class ResizeObserver {
  constructor(callback) {
    this.callback = callback;
    this.observables = new Map(); // 存储观察的元素
  }
  observe(target) {
    if (!this.observables.has(target)) {
      const update = () => {
        try {
          if (target && target.getBoundingClientRect && document.body.contains(target)) {
            const rect = target.getBoundingClientRect();
            this.callback([{ target, contentRect: rect }]);
          }
        } catch (error) {
          console.warn('ResizeObserver error:', error);
        }
      };
      this.observables.set(target, update);
      window.requestAnimationFrame(update);
    }
  }
  unobserve(target) {
    this.observables.delete(target);
  }
  disconnect() {
    this.observables.clear();
  }
};

const app = createApp(App)
const pinia = createPinia()

app.use(pinia)
app.use(router)
app.use(ElementPlus)

app.mount('#app') 